.App {
  text-align: center;
}

.carousel-caption {
  position: relative;
  padding: 20px;
  background: #32333e;
  bottom: 40px;
  z-index: 5;
}

.navText {
  color: white !important;
}

a {
  text-decoration: none;
}

video {
  margin-bottom: 30px;
}

.writtenInstr, .ingredients {
  text-align: left;
  margin: 50px;
  line-height: 1.5;
}

/* Container holding the image and the text */
.homepageImg {
  position: relative;
  text-align: center;
  color: white;
}

.homepageImg img {
  /* opacity: 0.7; */
  width: 100%;
}

/* Centered text */
.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.homepageBtn {
  background-color: #3f51b5 !important;
  color: white !important;
  margin: 10px !important;
  padding: 12px !important;
}

.homepageBtn:hover {
  background-color: #324090 !important;
}

.homeBtns {
  text-align: center;
}

.registerForm {
  margin: 30px;
}

.registerBtn {
  margin: 15px !important;
}

.registerConfirmTxt {
  font-weight: bold !important;
}

.success {
  width: 75%
}

.langSwitcher {
  float: right !important;
}

/* 404 page */
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
}

#notfound {
  position: relative;
  height: 100vh;
}

#notfound .notfound {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.notfound {
  max-width: 410px;
  width: 100%;
  text-align: center;
}

.notfound .notfound-404 {
  height: 280px;
  position: relative;
  z-index: -1;
}

.notfound .notfound-404 h2 {
  font-family: 'Montserrat', sans-serif;
  font-size: 230px;
  margin: 0px;
  font-weight: 900;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  background: url('./img/bg.jpg') no-repeat;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: cover;
  background-position: center;
}


.notfound h2 {
  font-family: 'Montserrat', sans-serif;
  color: #000;
  font-size: 24px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 0;
}

.notfound p {
  font-family: 'Montserrat', sans-serif;
  color: #000;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 20px;
  margin-top: 0px;
}

.notfound a {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
  background: #0046d5;
  display: inline-block;
  padding: 15px 30px;
  border-radius: 40px;
  color: #fff;
  font-weight: 700;
  -webkit-box-shadow: 0px 4px 15px -5px #0046d5;
          box-shadow: 0px 4px 15px -5px #0046d5;
}


@media only screen and (max-width: 767px) {
    .notfound .notfound-404 {
      height: 142px;
    }
    .notfound .notfound-404 h1 {
      font-size: 112px;
    }
}
